import React, { useState } from 'react';

export default function NewsletterSignup() {
  const [email, setEmail] = useState("");
  const [submitted, setSubmitted] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmitted(true);
  };

  return (
    <section className="relative bg-gradient-to-r from-pink-100 to-yellow-50 py-12 px-6 rounded-2xl shadow-glass my-12 overflow-hidden">
      <div className="flex flex-col md:flex-row items-center justify-between gap-8 max-w-5xl mx-auto">
        {/* Image on left for desktop, top for mobile */}
        <div className="md:w-1/2 w-full flex justify-center mb-6 md:mb-0">
          <img
            src="/images/newsletter-flower.jpg"
            alt="Fragrance flowers"
            className="rounded-xl shadow-lg w-64 h-64 object-cover border-4 border-white"
            style={{ fontFamily: 'inherit' }}
          />
        </div>
        <div className="md:w-1/2 w-full text-center md:text-left">
          <h2 className="text-3xl md:text-4xl font-bold mb-3 text-pink-700" style={{ fontFamily: 'Montserrat, Inter, sans-serif' }}>
            Join the SBK Fragrances Family
          </h2>
          <p className="mb-5 text-gray-700 text-lg" style={{ fontFamily: 'Inter, sans-serif' }}>
            Sign up for our newsletter and get <span className="font-semibold text-pink-600">exclusive offers</span>, fragrance tips, and early access to new arrivals!
          </p>
          {submitted ? (
            <p className="text-green-600 font-semibold text-lg">Thank you for subscribing!</p>
          ) : (
            <form onSubmit={handleSubmit} className="flex flex-col md:flex-row gap-3 items-center md:items-end justify-center md:justify-start">
              <input
                type="email"
                required
                value={email}
                onChange={e => setEmail(e.target.value)}
                placeholder="Enter your email"
                className="px-5 py-3 rounded-full border border-gray-300 focus:outline-none focus:ring-2 focus:ring-pink-400 w-full md:w-auto text-base"
                style={{ fontFamily: 'Inter, sans-serif' }}
              />
              <button
                type="submit"
                className="bg-pink-500 text-white px-8 py-3 rounded-full font-semibold hover:bg-pink-600 transition-colors shadow"
                style={{ fontFamily: 'Montserrat, Inter, sans-serif' }}
              >
                Subscribe
              </button>
            </form>
          )}
        </div>
      </div>
    </section>
  );
}
